.wrapper {
  position: sticky;
  box-sizing: border-box;
  bottom: 0;
  background-color: #ffffff;
  height: 70px;
  width: 100%;
  box-shadow: 0px -4px 20px rgba(0, 0, 0, 0.04);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  max-width: 500px;
}